@import "../../common/syles/sass/variables.scss";
@import "../../common/syles/sass/mixins";

.work {
  width: 450px;
  background-color: $bgItemColor;
  @include flex(column,center,center,wrap);
  text-align: center;
  margin-bottom: 20px;
  @media screen and (max-width: 600px) {
    width: 250px;
  }

  h4 {
    @extend TitleItem;
    margin-bottom: 20px;
    margin-top: 20px;
  }

  p {
    @extend textItem;
    margin-top: 0;
    display: block;
    margin-block-end: 20px;
     }

  .imgContainer {
    background-color: $bgColor;
    height: 350px;
    width: 100%;
    @include flex(column,center,center,wrap);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    @media screen and (max-width: 600px) {
      height: 200px;
    }

    &::before {
      @include blackout(0.3)
    }


  }
}
.button{
  background:$bgItemColor ;
  color: aqua;
}




