@import "../common/syles/sass/variables.scss";
@import "../common/syles/sass/mixins";
@import "../common/components/button/Button.module";

.freelancerBlock {

  padding: 40px 0px;
  background-color: $bgSecondColor;
  text-align: center;

  .freelancerContainer {
    @extend container;
    height: 100%;
    @include flex(column, space-around, center, wrap);
    padding-bottom: 20px;
    h2 {
      @extend TitleItem;
      color: $textColor ;
      font-size: 1.5em;
    }
    btn{
      @extend .btn;
      &:hover {
        cursor: pointer;
      }

    }
  }

}
.active{
  color: $textColor;
}


