@import "../../common/syles/sass/variables.scss";
@import "../../common/syles/sass/mixins";

.title {
    @extend TitleText;
    margin-bottom: 0;
    margin-top: 0;
    display: block;
    position: relative;
     h2{
        margin-block-start: 20px;
        margin-block-end: 40px;
    }
    &::before {
        content: "";
        position: absolute;
        background:  $primaryColor;
        width: 55px;
        height: 2px;
        bottom:20px;
        left: 50%;
        transform: translate(-50%,50%);
    }
}



