@import './variables.scss';

@mixin text($fontFamily,$fontSize,$fontWeight,$color,$lineHeight) {
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  color: $color;
  line-height:$lineHeight
}
@mixin blackout($opacity) {
  position: absolute;
  content: "";
  background-color: black;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity:$opacity;
}
@mixin flex($direction,$justifyContent,$alignItems,$wrap) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justifyContent;
  align-items: $alignItems;
  flex-wrap: $wrap;
}
textItem {
  color: $textColor;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  font-family:$TextFontFamily;
}

TitleItem {
  color: $primaryColor;
  font-size: 20px;
  font-weight: 600;
  line-height: 1;
  font-family: oswald, sans-serif;
}

TitleText {
  color: $textColor;
  font-size: 32px;
  font-weight: 600;
  font-family: oswald, sans-serif;
  text-transform: uppercase;
}
myButton {
  background:$primaryColor ;
  width: 150px;
  height: 75px;
  color:$bgItemColor ;
  font-size: 20px;
  font-weight: 600;
  display: block;
  line-height: 1;
  font-family: rubik, sans-serif;

}

  container {
    width: 80%;
    margin: 0 auto;

  }


